.Pagination {
  display: flex;
  margin-bottom: 3rem;
  align-items: center;
  justify-content: center; }
  .Pagination a,
  .Pagination .disabled-btn {
    width: 2rem;
    height: 2rem;
    background: #495057;
    border-radius: 1rem;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.25rem; }
  .Pagination a {
    cursor: pointer; }
    .Pagination a:hover {
      background: #adb5bd; }
  .Pagination .disabled-btn {
    background: #ced4da;
    color: #e9ecef; }
  .Pagination .page {
    margin-left: 1rem;
    margin-right: 1rem;
    color: #495057; }
    .Pagination .page b {
      color: #212529;
      font-weight: 600; }
