.AskRemove {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  justify-content: center; }
  .AskRemove .box {
    border-radius: 4px;
    width: 320px;
    padding: 1rem 1rem;
    background: white;
    border: 1px solid #ced4da; }
    .AskRemove .box .message {
      font-weight: 600; }
    .AskRemove .box .buttons {
      margin-top: 2rem;
      display: flex;
      justify-content: flex-end; }
      .AskRemove .box .buttons button {
        padding: 0.25rem 0.5rem;
        font-weight: 600;
        font-size: 0.875rem;
        cursor: pointer; }
        .AskRemove .box .buttons button.cancel {
          border: 1px solid #343a40; }
          .AskRemove .box .buttons button.cancel:hover {
            background: #f8f9fa; }
        .AskRemove .box .buttons button.confirm {
          background: #343a40;
          color: white; }
          .AskRemove .box .buttons button.confirm:hover {
            background: #868e96; }
      .AskRemove .box .buttons button + button {
        margin-left: 0.5rem; }
