.EditorHead {
  background: rgba(255, 255, 255, 0.875);
  transition: 0.3s all ease-in;
  position: fixed;
  left: 0;
  width: 100%;
  z-index: 5;
  min-height: 0; }
  .EditorHead.scrolling {
    box-shadow: 0 4px 16px 0px rgba(181, 181, 181, 0.16); }
  .EditorHead .wrapper {
    padding-top: 1rem;
    padding-bottom: 1rem;
    display: flex;
    justify-content: flex-end; }
  .EditorHead button {
    display: flex;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    width: 5rem;
    line-height: 1;
    display: flex;
    justify-content: center;
    font-weight: 600;
    cursor: pointer; }
    .EditorHead button.cancel {
      border: 1px solid #495057;
      color: #495057; }
      .EditorHead button.cancel:hover {
        color: #fa5252;
        border: 1px solid #fa5252; }
    .EditorHead button.submit {
      background: #212529;
      color: white; }
      .EditorHead button.submit:hover {
        background: #495057; }
  .EditorHead button + button {
    margin-left: 0.5rem; }
  .EditorHead + .padding {
    height: 4.125rem; }
