.PostViewer {
  margin-top: 5rem;
  margin-bottom: 2.5rem; }
  .PostViewer .title {
    color: #212529;
    font-size: 3rem;
    margin-bottom: 0.5rem; }
  .PostViewer .meta {
    color: #868e96;
    font-style: italic; }
    .PostViewer .meta .separator {
      margin-left: 0.25rem;
      margin-right: 0.25rem; }
  .PostViewer .body {
    margin-top: 4rem;
    font-size: 1.125rem;
    color: #495057;
    line-height: 1.5; }
    .PostViewer .body blockquote {
      padding-left: 1rem;
      border-left: 4px solid #212529; }
