.Header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-bottom: 2rem; }
  .Header .logo {
    font-weight: 800;
    letter-spacing: 2px; }
  .Header .right-area .logged-in {
    display: flex;
    align-items: center; }
    .Header .right-area .logged-in .username {
      font-size: 0.875rem;
      font-weight: 600;
      color: #212529;
      margin-right: 0.5rem; }
  .Header .right-area a,
  .Header .right-area button {
    display: flex;
    align-items: center;
    cursor: pointer;
    border: 1px solid #343a40;
    height: 2rem;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    font-size: 0.875rem;
    border-radius: 1rem;
    font-weight: 600;
    color: #343a40; }
    .Header .right-area a:hover,
    .Header .right-area button:hover {
      background: #343a40;
      color: white; }
