.Editor .title {
  width: 100%;
  font-size: 2.5rem;
  font-weight: 800;
  border: none;
  outline: none;
  color: #212529; }
  .Editor .title::placeholder {
    font-weight: 500;
    color: #868e96; }

.Editor .quill-editor {
  padding-bottom: 3rem;
  cursor: text;
  min-height: 300px;
  color: #212529;
  margin-top: 2rem;
  font-size: 1.125rem; }
  .Editor .quill-editor p {
    line-height: 1.5; }
  .Editor .quill-editor .ql-editor {
    padding: 0; }
  .Editor .quill-editor .ql-blank::before {
    left: 0; }
