.PostList .post-item {
  padding-top: 2rem;
  padding-bottom: 2rem; }
  .PostList .post-item .title {
    color: #212529;
    font-size: 2rem;
    font-weight: 800; }
    .PostList .post-item .title:hover {
      color: #12b886; }
  .PostList .post-item .meta {
    margin-top: 0.5rem;
    color: #868e96;
    font-style: italic; }
    .PostList .post-item .meta .separator {
      margin-left: 0.25rem;
      margin-right: 0.25rem; }
  .PostList .post-item p {
    color: #495057;
    font-size: 1.125rem;
    line-height: 1.5; }

.PostList .post-item + .post-item {
  border-top: 1px solid #e9ecef; }
