.AuthForm h3 {
  margin: 0;
  display: inline-flex;
  color: #343a40;
  margin-bottom: 1rem; }

.AuthForm form {
  display: flex;
  flex-direction: column; }
  .AuthForm form input {
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid #adb5bd;
    padding-bottom: 0.5rem;
    outline: none; }
    .AuthForm form input:focus {
      color: #0ca678;
      border-bottom: 1px solid #0ca678; }
  .AuthForm form input + input {
    margin-top: 1rem; }
  .AuthForm form button {
    border-radius: 1.25rem;
    background: #212529;
    height: 2.5rem;
    font-size: 1rem;
    color: white;
    font-weight: 600;
    margin-top: 1rem;
    margin-bottom: 2rem;
    cursor: pointer; }
    .AuthForm form button:hover {
      background: #495057; }
  .AuthForm form .error {
    font-size: 0.875rem;
    color: #ff6b6b;
    text-align: center;
    margin-top: 1rem; }

.AuthForm .footer {
  text-align: right; }
  .AuthForm .footer a {
    font-weight: 600;
    text-decoration: underline;
    color: #868e96; }
    .AuthForm .footer a:hover {
      color: #212529; }
